// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'
import UiLoader from '@components/ui-loader'
// ** Store & Actions
import { GetUserPrize } from '../../../../../store/actions'
import { useSelector, useDispatch } from 'react-redux'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Input, Label, Row, Col, CardHeader, CardTitle } from 'reactstrap'
import { useParams } from 'react-router-dom'
import BreadCrumbs from '@components/breadcrumbs'
const DataTableTransaction = () => {

  // ** Store Vars

  const dispatch = useDispatch()
  const store = useSelector(state => state.users)
  const params = useParams()
  
  // ** States
  
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [searchValue, setSearchValue] = useState('')
  const [filteredData, setFilteredData] = useState([])

  // ** Get data on mount
  
  useEffect(() => {    
    dispatch(GetUserPrize(params.id, currentPage))
  }, [currentPage])

  // ** Function to handle filter

  const handleFilter = e => {
    const value = e.target.value
    let updatedData = []
    setSearchValue(value)
    if (value.length) {
      updatedData = store.prize.data.filter(item => {
        const startsWith =
          item.redeem_prizes_name?.toLowerCase().startsWith(value.toLowerCase()) ||
          item.category?.toLowerCase().startsWith(value.toLowerCase()) ||
          item.redeem_prizes_caption?.toLowerCase().startsWith(value.toLowerCase()) ||
          item.redeem_prizes_status.toLowerCase().startsWith(value.toLowerCase()) ||
          item.redeem_prizes_value.toString().toLowerCase().startsWith(value.toLowerCase()) 
        
        const includes =
          item.redeem_prizes_name?.toLowerCase().includes(value.toLowerCase()) ||
          item.category?.toLowerCase().includes(value.toLowerCase()) ||
          item.redeem_prizes_caption?.toLowerCase().includes(value.toLowerCase()) ||
          item.redeem_prizes_status.toLowerCase().includes(value.toLowerCase()) ||
          item.redeem_prizes_value.toString().toLowerCase().includes(value.toLowerCase()) 

        if (startsWith) {
          return startsWith
        } else if (!startsWith && includes) {
          return includes
        } else return null
      })
      setFilteredData(updatedData)
    }
  }
  
  // ** Function to handle Pagination and get data

  const handlePagination = page => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function to handle per page

  const handlePerPage = e => {
    setRowsPerPage(parseInt(e.target.value))
  }

  // ** Custom Pagination

  const CustomPagination = () => {

    const count = store.prize.last_page
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }

  // ** Table data to render
  
  const dataToRender = () => {
    if (store.prize.data?.length > 0) {
      return searchValue.length ? filteredData : store.prize.data?.slice(0, rowsPerPage)
    } else {
      return []
    } 
  }

  if (store.loadAllUsers) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  const columns = [
    {
      name: 'Sno',
      minWidth: '10px',
      selector: 's_no',
      sortable: true,
      cell: (row, pos) => `#${store.prize.from + pos}`
    }, 
    {
      name: 'Name',
      minWidth: '100px',
      selector: 'name',
      sortable: true,
      cell: (row) => row?.redeem_prizes_name
    },
    {
      name: 'Category',
      minWidth: '100px',
      selector: 'category',
      sortable: true,
      cell: (row) => row?.category
    },
    {
      name: 'Caption',
      minWidth: '100px',
      selector: 'redeem_prizes_caption',
      sortable: true,
      cell: (row) => row?.redeem_prizes_caption
    },
    {
      name: 'Status',
      minWidth: '100px',
      selector: 'status',
      sortable: true,
      cell: (row) => row?.redeem_prizes_status
    },
    {
      name: 'Prize Value',
      minWidth: '100px',
      selector: 'redeem_prizes_value',
      sortable: true,
      cell: (row) => row?.redeem_prizes_value
    }
  ]
  

  return (
    <Fragment>
    <BreadCrumbs breadCrumbTitle='Manage Users' breadCrumbParent='users'  breadCrumbActive='prize Redeemed' />
      <Card>
        <CardHeader className='border-bottom'>
          <CardTitle tag='h4'>prize Redeemed</CardTitle>
        </CardHeader>
        <Row className='mx-0 mt-1 mb-50'>
          <Col sm='6'>
            <div className='d-flex align-items-center'>
              <Label for='sort-select'>show</Label>
              <Input
                className='dataTable-select'
                type='select'
                id='sort-select'
                value={rowsPerPage}
                onChange={e => handlePerPage(e)}
              >
                <option value={10}>10</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </Input>
              <Label for='sort-select'>entries</Label>
            </div>
          </Col>
          <Col className='d-flex align-items-center justify-content-sm-end mt-sm-0 mt-1' sm='6'>
            <Label className='mr-1' for='search-input'>
              Search
            </Label>
            <Input
              className='dataTable-filter'
              type='text'
              bsSize='sm'
              id='search-input'
              value={searchValue}
              onChange={handleFilter}
            />
          </Col>
        </Row>
        <DataTable
          noHeader
          pagination
          paginationServer
          className='react-dataTable'
          columns={columns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={dataToRender()}
        />
      </Card>
    </Fragment>
  )
}

export default memo(DataTableTransaction)
